import { LoremIpsum } from "lorem-ipsum";

const lorem = new LoremIpsum({
  sentencesPerParagraph: {
    max: 8,
    min: 4,
  },
  wordsPerSentence: {
    max: 32,
    min: 8,
  },
});

export const Identifier = (length: number) => {
  let result = "";
  let characters =
    "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";
  let charactersLength = characters.length;
  for (let i = 0; i < length; i++) {
    result += characters.charAt(Math.floor(Math.random() * charactersLength));
  }
  return result;
};

export const Word = (number?: number) => lorem.generateWords(number);
export const Sentence = (number?: number) => lorem.generateSentences(number);
export const Paragraph = (number: number) => lorem.generateParagraphs(number);

const formatted = (text: string) => {
  const maxSpan = 13;
  const bold = [0, 0.1];
  const italic = [0.09, 0.12];
  const underline = [0.1, 0.18];

  let spans = [];
  let words = text.split(" ");
  let index = 0;
  while (index < words.length) {
    let spanLength = Math.random() * maxSpan + 1;
    let spanStart = index;
    let spanEnd = Math.min(words.length - 1, index + spanLength);
    index = spanEnd + 1;
    let span = words.slice(spanStart, spanEnd).join(" ");

    let roll = Math.random();
    if (roll >= bold[0] && roll <= bold[1]) span = `<b>${span}</b>`;
    if (roll >= italic[0] && roll <= italic[1]) span = `<i>${span}</i>`;
    if (roll >= underline[0] && roll <= underline[1]) span = `<u>${span}</u>`;

    spans.push(span);
  }

  return spans.join(" ");
};

export const FormattedSentence = (number: number) => {
  let spans = [];

  for (let index = 0; index < number; index++)
    spans.push(`<span>${formatted(lorem.generateSentences(1))}</span>`);

  return spans.join(" ");
};

export const FormattedParagraph = (number: number) => {
  let spans = [];

  for (let index = 0; index < number; index++)
    spans.push(`<p>${formatted(lorem.generateParagraphs(1))}</p>`);

  return spans.join(" ");
};

export const FormattedAnswerChoices = () =>
  `<br /><ol type="A"><li>${FormattedSentence(
    2 + Math.floor(Math.random() * 5)
  )}</li><br /><li>${FormattedSentence(
    2 + Math.floor(Math.random() * 5)
  )}</li><br /><li>${FormattedSentence(
    2 + Math.floor(Math.random() * 5)
  )}</li><br /><li>${FormattedSentence(
    2 + Math.floor(Math.random() * 5)
  )}</li></ol>`;
