import { Box, makeStyles, Typography } from "@material-ui/core";
import React from "react";

import { HeaderBanner } from "./HeaderBanner";
import { ScoreDistribution } from "../Graphics/ScoreDistribution";
import { SectionResults } from "../Data/Sections";

const useStyles = makeStyles({
  header: {
    color: "white",
    paddingBottom: 20,
    textAlign: "center",
  },
  headerTitle: {
    padding: 25,
  },
  scoreComponent: {
    maxWidth: 250,
    "& svg": {
      marginLeft: "auto",
      marginRight: "auto",
    },
  },
  verticalStack: {
    display: "flex",
    flexDirection: "column",
  },
  horizontalStack: {
    display: "flex",
    flexDirection: "row",
  },
  even: {
    justifyContent: "space-around",
  },
  spread: {
    justifyContent: "space-between",
  },
  overallLabel: {
    textAlign: "center",
  },
  navButton: {
    cursor: "pointer",
    "&:hover": {
      textDecoration: "underline",
    },
  },
});

export interface ReportHeaderProperties {
  onNavigate: (to: string) => void;
}

export const ReportHeader = ({ onNavigate }: ReportHeaderProperties) => {
  const classes = useStyles();

  return (
    <HeaderBanner>
      <Box className={`${classes.header} ${classes.verticalStack}`}>
        <Typography variant="h4" className={classes.headerTitle}>
          MCAT Practice Exam Results
        </Typography>
        <Box className={`${classes.horizontalStack} ${classes.even}`}>
          {SectionResults.map((section) => (
            <Box
              key={section.id}
              className={`${classes.scoreComponent} ${classes.verticalStack}`}
            >
              <ScoreDistribution
                height={125}
                width={200}
                margin={{ top: 10, left: 10, bottom: 20, right: 10 }}
                leftLabel={section.minScaledScore.toString()}
                rightLabel={section.maxScaledScore.toString()}
                score={section.scaledScore.toString()}
                percentage={section.percentage}
                textColor="white"
                textSize="default"
              />
              <Typography
                onClick={() => onNavigate(section.id)}
                className={classes.navButton}
              >
                {section.name}
              </Typography>
            </Box>
          ))}
        </Box>
      </Box>
    </HeaderBanner>
  );
};
