import {
  Grid,
  Link,
  makeStyles,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableRow,
} from "@material-ui/core";
import React from "react";
import FlagIcon from "@material-ui/icons/Flag";
import { SectionResults } from "../Data/Sections";
import moment from "moment";

const useStyles = makeStyles({
  root: {
    "& td:not(:first-child)": {
      textAlign: "center",
    },
    "& a": {
      cursor: "pointer",
    },
  },
});

export interface SectionSummaryProperties {
  onNavigate: (to: string) => void;
}

export const SectionSummary = ({ onNavigate }: SectionSummaryProperties) => {
  const classes = useStyles();

  return (
    <Grid container direction="column" className={classes.root}>
      <Grid item>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell>Section</TableCell>
              <TableCell>Score</TableCell>
              <TableCell>Percentile</TableCell>
              <TableCell>Correct</TableCell>
              <TableCell>Incomplete</TableCell>
              <TableCell>Average TPQ</TableCell>
              <TableCell>
                <FlagIcon /> Correct
              </TableCell>
              <TableCell>
                <FlagIcon /> Incorrect
              </TableCell>
              <TableCell></TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {SectionResults.map((section) => (
              <TableRow key={section.id}>
                <TableCell>
                  <Link onClick={() => onNavigate(section.id)}>
                    {section.name}
                  </Link>
                </TableCell>
                <TableCell>{section.scaledScore}</TableCell>
                <TableCell>{section.percentage}</TableCell>
                <TableCell>
                  {section.questionsCorrect} of {section.totalQuestions}
                </TableCell>
                <TableCell>{section.incomplete}</TableCell>
                <TableCell>
                  {moment
                    .utc(
                      moment
                        .duration(
                          section.averageQuestionTimeInSeconds,
                          "seconds"
                        )
                        .asMilliseconds()
                    )
                    .format("m:ss")}
                </TableCell>
                <TableCell>{section.flaggedCorrect}</TableCell>
                <TableCell>{section.flaggedIncorrect}</TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </Grid>
    </Grid>
  );
};
