import { Box, IconButton, makeStyles, Typography } from "@material-ui/core";
import VisibilityIcon from "@material-ui/icons/Visibility";
import React, { useState } from "react";
import { ItemResults } from "../Data/Items";
import { SkillsGroups } from "../Data/Skills";
import { ContentAreaBarGraph } from "../Graphics/ContentAreaBarGraph";
import { ItemReview } from "./ItemReview";
import { SkillsBreakdownTable } from "./SkillsBreakdownTable";

const useStyles = makeStyles({
  verticalStack: {
    display: "flex",
    flexDirection: "column",
  },
  horizontalStack: {
    display: "flex",
    flexDirection: "row",
  },
  skillsGroup: {
    marginTop: 15,
    marginBottom: 15,
  },
  graphContainer: {
    marginTop: "auto",
    marginBottom: "auto",
  },
  overallLabel: {
    marginTop: 30,
    paddingBottom: 10,
    borderBottom: "1px solid lightgray",
  },
  mainLabel: {
    marginTop: "auto",
    marginBottom: "auto",
    marginLeft: 30,
    marginRight: 30,
  },
});

export interface SkillsBreakdownProperties {
  sectionId: string;
}

export const SkillsBreakdown = ({ sectionId }: SkillsBreakdownProperties) => {
  const classes = useStyles();
  const [reviewIndexStart, setReviewIndexStart] = useState(0);
  const [reviewIndexEnd, setReviewIndexEnd] = useState(0);
  const [reviewIndex, setReviewIndex] = useState<number | undefined>();

  const items = ItemResults.filter((i) => i.section === sectionId);

  const review = (startIndex: number, numberOfItems: number) => {
    setReviewIndexStart(startIndex);
    setReviewIndexEnd(startIndex + numberOfItems - 1);
    setReviewIndex(startIndex);
  };

  return (
    <Box>
      {SkillsGroups.map((group, index) => (
        <Box
          key={index}
          className={`${classes.verticalStack} ${classes.skillsGroup}`}
        >
          <Box className={`${classes.horizontalStack} ${classes.overallLabel}`}>
            <Typography
              variant="h4"
              display="inline"
              className={classes.mainLabel}
            >
              {group.label}
            </Typography>
            <Typography variant="subtitle1" display="inline">
              {group.description}
            </Typography>
            <IconButton onClick={() => review(0, group.totalItems())}>
              <VisibilityIcon />
            </IconButton>
          </Box>
          <Box className={classes.horizontalStack}>
            <Box
              className={`${classes.verticalStack} ${classes.graphContainer}`}
            >
              <ContentAreaBarGraph
                width={300}
                height={300}
                margin={{ top: 50, left: 10, bottom: 50, right: 10 }}
                skills={group.skills}
              />
            </Box>
            <SkillsBreakdownTable skills={group.skills} onReview={review} />
          </Box>
        </Box>
      ))}
      <ItemReview
        open={reviewIndex !== undefined}
        onClose={() => setReviewIndex(undefined)}
        section={sectionId}
        itemNumber={items[reviewIndex || 0].number}
        canNext={(reviewIndex || 0) < reviewIndexEnd}
        canPrevious={(reviewIndex || 0) > reviewIndexStart}
        onNext={() => setReviewIndex((reviewIndex || 0) + 1)}
        onPrevious={() => setReviewIndex((reviewIndex || 0) - 1)}
        onFirst={() => setReviewIndex(reviewIndexStart)}
        onLast={() => setReviewIndex(reviewIndexEnd)}
        title={`Item ${(reviewIndex || 0) + 1 - reviewIndexStart} of ${
          reviewIndexEnd - reviewIndexStart + 1
        } (Item ${items[reviewIndex || 0].number})`}
      />
    </Box>
  );
};
