import {
  Box,
  IconButton,
  makeStyles,
  Paper,
  Typography,
} from "@material-ui/core";
import React, { useState } from "react";
import VisibilityIcon from "@material-ui/icons/Visibility";
import { CandidateHighlights } from "../Data/Highlights";
import { ItemReview } from "./ItemReview";

const useStyles = makeStyles({
  itemHighlight: {
    padding: 15,
    marginBottom: 15,
  },
  itemHighlightHeader: {
    margin: 5,
  },
  highlightContent: {
    borderLeft: "3px solid #F8B332",
    marginTop: 10,
    marginBottom: 20,
    marginLeft: 25,
    paddingTop: 1,
    paddingBottom: 1,
  },
  previewButton: {
    float: "right",
  },
});

export interface HighlightsProperties {
  sectionId: string;
}

export const Highlights = ({ sectionId }: HighlightsProperties) => {
  const classes = useStyles();
  const itemHighlights = CandidateHighlights.filter(
    (h) => h.section === sectionId
  );

  const [reviewIndex, setReviewIndex] = useState<number | undefined>();

  return (
    <>
      {itemHighlights.map((itemHighlight, index) => (
        <Paper
          elevation={3}
          key={`item-${itemHighlight.number}`}
          className={classes.itemHighlight}
        >
          <Typography
            display="inline"
            variant="h6"
            className={classes.itemHighlightHeader}
          >
            Item {itemHighlight.number}
          </Typography>
          <IconButton
            className={classes.previewButton}
            onClick={() => setReviewIndex(index)}
          >
            <VisibilityIcon />
          </IconButton>
          {itemHighlight.highlights.map((highlight, index) => (
            <Box key={index} className={classes.highlightContent}>
              <blockquote
                dangerouslySetInnerHTML={{ __html: highlight }}
              ></blockquote>
            </Box>
          ))}
        </Paper>
      ))}
      <ItemReview
        open={reviewIndex !== undefined}
        onClose={() => setReviewIndex(undefined)}
        section={sectionId}
        itemNumber={itemHighlights[reviewIndex || 0].number}
        canNext={(reviewIndex || 0) < itemHighlights.length - 1}
        canPrevious={(reviewIndex || 0) > 0}
        onNext={() => setReviewIndex((reviewIndex || 0) + 1)}
        onPrevious={() => setReviewIndex((reviewIndex || 0) - 1)}
        onFirst={() => setReviewIndex(0)}
        onLast={() => setReviewIndex(itemHighlights.length - 1)}
        title={`Highlight ${(reviewIndex || 0) + 1} of ${
          itemHighlights.length
        } (Item ${itemHighlights[reviewIndex || 0].number})`}
      />
    </>
  );
};
