import {
  IconButton,
  makeStyles,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableRow,
  Typography,
} from "@material-ui/core";
import React, { useState } from "react";
import FlagIcon from "@material-ui/icons/Flag";
import CheckIcon from "@material-ui/icons/Check";
import ClearIcon from "@material-ui/icons/Clear";
import CheckCircleIcon from "@material-ui/icons/CheckCircle";
import CancelIcon from "@material-ui/icons/Cancel";
import FilterListIcon from "@material-ui/icons/FilterList";
import FlagOutlinedIcon from "@material-ui/icons/FlagOutlined";
import VisibilityIcon from "@material-ui/icons/Visibility";
import moment from "moment";
import { ItemData, ItemResults } from "../Data/Items";
import { ItemReview } from "./ItemReview";

const useStyles = makeStyles({
  centered: {
    textAlign: "center",
  },
});

export interface ItemsTableProperties {
  sectionId: string;
}

export const ItemsTable = ({ sectionId }: ItemsTableProperties) => {
  const classes = useStyles();
  const itemData = ItemResults.filter((i) => i.section === sectionId);

  const [reviewItem, setReviewItem] = useState<number | undefined>();
  const [items, setItems] = useState<Array<ItemData>>(itemData);
  const [correctnessFilter, setCorrectnessFilter] = useState<string>("none");
  const [flaggedFilter, setFlaggedFilter] = useState<string>("none");

  const nextCorrectnessFilter = () => {
    let newFilter =
      correctnessFilter === "correct"
        ? "incorrect"
        : correctnessFilter === "incorrect"
        ? "none"
        : "correct";
    setCorrectnessFilter(newFilter);
    setItems(getItemData(newFilter, flaggedFilter));
  };

  const nextFlaggedFilter = () => {
    let newFilter =
      flaggedFilter === "flagged"
        ? "unflagged"
        : flaggedFilter === "unflagged"
        ? "none"
        : "flagged";
    setFlaggedFilter(newFilter);
    setItems(getItemData(correctnessFilter, newFilter));
  };

  const getItemData = (correctnessFilter: string, flaggedFilter: string) =>
    itemData
      .filter(
        (item) =>
          correctnessFilter === "none" ||
          (correctnessFilter === "correct" && item.correct) ||
          (correctnessFilter === "incorrect" && !item.correct)
      )
      .filter(
        (item) =>
          flaggedFilter === "none" ||
          (flaggedFilter === "flagged" && item.flagged) ||
          (flaggedFilter === "unflagged" && !item.flagged)
      );

  return (
    <>
      <Table>
        <TableHead>
          <TableRow>
            <TableCell className={classes.centered}>
              <Typography>#</Typography>
            </TableCell>
            <TableCell className={classes.centered}>
              <IconButton onClick={nextCorrectnessFilter}>
                {correctnessFilter === "none" && <FilterListIcon />}
                {correctnessFilter === "correct" && <CheckCircleIcon />}
                {correctnessFilter === "incorrect" && <CancelIcon />}
              </IconButton>
            </TableCell>
            <TableCell>
              <Typography>Preview</Typography>
            </TableCell>
            <TableCell className={classes.centered}>
              <Typography>Time</Typography>
            </TableCell>
            <TableCell className={classes.centered}>
              <IconButton onClick={nextFlaggedFilter}>
                {flaggedFilter === "none" && <FilterListIcon />}
                {flaggedFilter === "flagged" && <FlagIcon />}
                {flaggedFilter === "unflagged" && <FlagOutlinedIcon />}
              </IconButton>
            </TableCell>
            <TableCell className={classes.centered}>
              <VisibilityIcon />
            </TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {items.map((item, index) => (
            <TableRow key={item.number}>
              <TableCell className={classes.centered}>
                <Typography>{item.number}</Typography>
              </TableCell>
              <TableCell className={classes.centered}>
                {item.correct ? <CheckIcon /> : <ClearIcon />}
              </TableCell>
              <TableCell>
                <Typography>{item.preview}</Typography>
              </TableCell>
              <TableCell className={classes.centered}>
                <Typography>
                  {moment
                    .utc(
                      moment
                        .duration(item.timeInSeconds, "seconds")
                        .asMilliseconds()
                    )
                    .format("m:ss")}
                </Typography>
              </TableCell>
              <TableCell className={classes.centered}>
                {item.flagged && <FlagIcon />}
              </TableCell>
              <TableCell className={classes.centered}>
                <IconButton onClick={() => setReviewItem(index)}>
                  <VisibilityIcon />
                </IconButton>
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
      <ItemReview
        open={reviewItem !== undefined}
        onClose={() => setReviewItem(undefined)}
        section={sectionId}
        itemNumber={items[reviewItem || 0].number}
        canNext={(reviewItem || 0) < items.length - 1}
        canPrevious={(reviewItem || 0) >= 1}
        onNext={() => setReviewItem((reviewItem || 0) + 1)}
        onPrevious={() => setReviewItem((reviewItem || 0) - 1)}
        onFirst={() => setReviewItem(0)}
        onLast={() => setReviewItem(items.length - 1)}
        title={
          items.length === itemData.length
            ? `Item ${(reviewItem || 0) + 1} of ${items.length}`
            : `${(reviewItem || 0) + 1} of ${items.length} (Item ${
                items[reviewItem || 0].number
              })`
        }
      />
    </>
  );
};
