import React, { useState } from "react";
import { Box, Container, Slide } from "@material-ui/core";

import { ReportHeader } from "./Header/ReportHeader";
import { Summary } from "./Summary/Summary";
import { SectionDetails } from "./Section/SectionDetails";
import { OverallDataKey } from "./Data/Overall";
import { SectionResults } from "./Data/Sections";

export const App = () => {
  const [viewing, setViewing] = useState(OverallDataKey);

  return (
    <>
      <ReportHeader onNavigate={(to: string) => setViewing(to)} />
      <Container>
        {viewing === OverallDataKey && (
          <Slide direction="right" in={true}>
            <Box>
              <Summary onNavigate={(to: string) => setViewing(to)} />
            </Box>
          </Slide>
        )}
        {SectionResults.map((section) => (
          <Box key={section.id}>
            {viewing === section.id && (
              <Slide direction="left" in={true}>
                <Box>
                  <SectionDetails
                    onNavigate={(to: string) => setViewing(to)}
                    section={section}
                  />
                </Box>
              </Slide>
            )}
          </Box>
        ))}
      </Container>
    </>
  );
};
