import {
  Box,
  Button,
  Card,
  CardContent,
  Grid,
  makeStyles,
  Slide,
  Tab,
  Tabs,
  Typography,
} from "@material-ui/core";
import React from "react";
import ArrowBackIcon from "@material-ui/icons/ArrowBack";
import FlagIcon from "@material-ui/icons/Flag";
import { OverallDataKey } from "../Data/Overall";
import { SectionData } from "../Data/Sections";
import { ScoreDistribution } from "../Graphics/ScoreDistribution";
import { ItemsTable } from "./ItemsTable";
import { Highlights } from "./Highlights";
import { SkillsBreakdown } from "./SkillsBreakdown";
import { ScaledScoreHelp } from "../Help/ScaledScore";
import { PercentileRankHelp } from "../Help/PercentileRanks";

const useStyles = makeStyles({
  verticalStack: {
    display: "flex",
    flexDirection: "column",
  },
  horizontalStack: {
    display: "flex",
    flexDirection: "row",
  },
  backNavigation: {
    marginTop: -50,
  },
  detailCard: {
    textAlign: "center",
    verticalAlign: "middle",
    margin: 10,
    minWidth: 210,
  },
  scoreCurve: {
    "& svg": {
      margin: "auto",
    },
  },
});

export interface SectionDetailsProperties {
  onNavigate: (to: string) => void;
  section: SectionData;
}

export const SectionDetails = ({
  onNavigate,
  section,
}: SectionDetailsProperties) => {
  const classes = useStyles();
  const [tab, setTab] = React.useState(0);

  const onTabChange = (event: React.ChangeEvent<{}>, newValue: number) => {
    setTab(newValue);
  };
  return (
    <>
      <Button
        onClick={() => onNavigate(OverallDataKey)}
        className={classes.backNavigation}
      >
        <ArrowBackIcon />
        <Typography>Back To Summary</Typography>
      </Button>
      <Box className={classes.verticalStack}>
        <Box className={classes.horizontalStack}>
          <Slide direction="right" in={true} timeout={500}>
            <Grid
              container
              direction="row"
              justify="center"
              alignItems="center"
            >
              <Grid item>
                <Card variant="outlined" className={classes.detailCard}>
                  <CardContent>
                    <Typography variant="h4">{section.scaledScore}</Typography>
                    <Typography variant="subtitle2" display="inline">
                      Scaled Score
                    </Typography>
                    <ScaledScoreHelp forSection={true} />
                  </CardContent>
                </Card>
              </Grid>
              <Grid item>
                <Card variant="outlined" className={classes.detailCard}>
                  <CardContent>
                    <Typography variant="h4">{section.percentage}%</Typography>
                    <Typography variant="subtitle2" display="inline">
                      Percentile Rank
                    </Typography>
                    <PercentileRankHelp />
                  </CardContent>
                </Card>
              </Grid>
              <Grid item>
                <Card variant="outlined" className={classes.detailCard}>
                  <CardContent>
                    <Typography variant="h4">
                      {section.questionsCorrect} of {section.totalQuestions}
                    </Typography>
                    <Typography variant="subtitle2">
                      Questions Correct
                    </Typography>
                  </CardContent>
                </Card>
              </Grid>
              <Grid item>
                <Card variant="outlined" className={classes.detailCard}>
                  <CardContent>
                    <Typography variant="h4">{section.incomplete}</Typography>
                    <Typography variant="subtitle2">
                      Questions Incomplete
                    </Typography>
                  </CardContent>
                </Card>
              </Grid>
              <Grid item>
                <Card variant="outlined" className={classes.detailCard}>
                  <CardContent>
                    <Typography variant="h4">
                      {section.flaggedCorrect}
                    </Typography>
                    <Typography variant="subtitle2">
                      <FlagIcon /> Correct
                    </Typography>
                  </CardContent>
                </Card>
              </Grid>
              <Grid item>
                <Card variant="outlined" className={classes.detailCard}>
                  <CardContent>
                    <Typography variant="h4">
                      {section.flaggedIncorrect}
                    </Typography>
                    <Typography variant="subtitle2">
                      <FlagIcon /> Incorrect
                    </Typography>
                  </CardContent>
                </Card>
              </Grid>
            </Grid>
          </Slide>
          <Box className={`${classes.scoreCurve} ${classes.verticalStack}`}>
            <ScoreDistribution
              height={250}
              width={500}
              margin={{ top: 10, left: 10, bottom: 20, right: 10 }}
              leftLabel={section.minScaledScore.toString()}
              rightLabel={section.maxScaledScore.toString()}
              score={section.scaledScore.toString()}
              percentage={section.percentage}
            />
          </Box>
        </Box>
        <Tabs value={tab} onChange={onTabChange}>
          <Tab label="Question Review" />
          <Tab label="Highlights" />
          <Tab label="Content & Skills Breakdown" />
        </Tabs>
        {tab === 0 && (
          <Slide direction="up" in={true} timeout={500}>
            <Box>
              <ItemsTable sectionId={section.id} />
            </Box>
          </Slide>
        )}
        {tab === 1 && (
          <Slide direction="up" in={true} timeout={500}>
            <Box>
              <Highlights sectionId={section.id} />
            </Box>
          </Slide>
        )}
        {tab === 2 && (
          <Slide direction="up" in={true} timeout={500}>
            <Box>
              <SkillsBreakdown sectionId={section.id} />
            </Box>
          </Slide>
        )}
      </Box>
    </>
  );
};
