import { Skill } from "./Skills";
import { FormattedAnswerChoices, FormattedParagraph, Sentence } from "./Text";

const previewLength = 250;

export class ItemData {
  public preview: string;
  public content: string;
  public correctAnswer: string;
  public answer: string;
  public answerRationales: Array<string>;
  public skills: Array<Skill>;

  constructor(
    public number: number,
    public section: string,
    public correct: boolean,
    public timeInSeconds: number,
    public flagged: boolean
  ) {
    let content = `${FormattedParagraph(
      3 + Math.floor(Math.random() * 4)
    )}${FormattedAnswerChoices()}`.split(" ");
    for (let chance = 0.15; chance > 0; chance -= 0.1)
      if (Math.random() >= chance) {
        let length = 30 + Math.round(Math.random() * 100);
        let start = Math.round(Math.random() * content.length) - length;
        content = [
          ...content.slice(0, start),
          "<mark>",
          ...content.slice(start, start + length),
          "</mark>",
          ...content.slice(start + length),
        ];
      }
    this.content = content.join(" ");
    this.preview = `${Sentence(2).substr(0, previewLength)}...`;

    this.skills = [...new Array(2 + Math.round(Math.random() * 3))].map(
      () => new Skill()
    );

    let answers = ["A", "B", "C", "D"];
    let correctAnswerIndex = Math.min(3, Math.round(Math.random() * 3));
    this.correctAnswer = answers[correctAnswerIndex];
    if (this.correct) this.answer = this.correctAnswer;
    else
      this.answer =
        Math.random() < 0.2
          ? ""
          : [
              ...answers.slice(0, correctAnswerIndex),
              ...answers.slice(correctAnswerIndex + 1, answers.length),
            ][Math.min(2, Math.round(Math.random() * 2))];

    this.answerRationales = [...new Array(4)].map(() => Sentence(2));
  }
}

const createItems = (count: number, section: string) => {
  let items = [];
  for (let number = 1; number <= count; number++)
    items.push(
      new ItemData(
        number,
        section,
        Math.random() >= 0.25,
        Math.random() * 360,
        Math.random() >= 0.85
      )
    );
  return items;
};

export const ItemResults: Array<ItemData> = [
  ...createItems(59, "CPFBS"),
  ...createItems(53, "CARS"),
  ...createItems(59, "BBFLS"),
  ...createItems(59, "PSBFB"),
];
