import { FormattedParagraph, FormattedSentence } from "./Text";

export class ItemHighlight {
  constructor(
    public number: number,
    public section: string,
    public highlights: Array<string>
  ) {}
}

const createHighlight = (maxItemNumber: number, section: string) => {
  const paragraphRange = [1, 2];
  const sentenceRange = [2, 5];
  const highlightsRange = [1, 3];

  const generateParagraphHighlight = () =>
    FormattedParagraph(
      Math.max(paragraphRange[0], Math.ceil(Math.random() * paragraphRange[1]))
    );
  const generateSentenceHighlight = () =>
    FormattedSentence(
      Math.max(sentenceRange[0], Math.ceil(Math.random() * sentenceRange[1]))
    );
  const generateHighlight = () =>
    Math.random() >= 0.5
      ? generateParagraphHighlight()
      : generateSentenceHighlight();
  const generateHighlights = () =>
    [
      ...new Array(
        highlightsRange[0] + Math.floor(Math.random() * highlightsRange[1])
      ),
    ].map(generateHighlight);

  let item = Math.ceil(Math.random() * (maxItemNumber - 1));

  return new ItemHighlight(item, section, generateHighlights());
};

const createItemHighlights = (
  number: number,
  maxItem: number,
  section: string
) =>
  [...new Array(number)]
    .map(() => createHighlight(maxItem, section))
    .sort((a, b) => a.number - b.number);

export const CandidateHighlights: Array<ItemHighlight> = [
  ...createItemHighlights(5, 59, "CPFBS"),
  ...createItemHighlights(2, 53, "CARS"),
  ...createItemHighlights(7, 59, "BBFLS"),
  ...createItemHighlights(4, 59, "PSBFB"),
];
