import {
  Box,
  Card,
  CardContent,
  Grid,
  makeStyles,
  Slide,
  Typography,
} from "@material-ui/core";
import moment from "moment";
import React from "react";

import { OverallResults } from "../Data/Overall";
import { ScoreDistribution } from "../Graphics/ScoreDistribution";
import { ScaledScoreHelp } from "../Help/ScaledScore";
import { PercentileRankHelp } from "../Help/PercentileRanks";

const useStyles = makeStyles({
  root: {
    marginBottom: 20,
  },
  verticalStack: {
    display: "flex",
    flexDirection: "column",
  },
  horizontalStack: {
    display: "flex",
    flexDirection: "row",
  },
  even: {
    justifyContent: "space-around",
  },
  spread: {
    justifyContent: "space-between",
  },
  overallLabel: {
    textAlign: "center",
  },
  detailsGrid: {
    marginLeft: 25,
  },
  detailCard: {
    textAlign: "center",
    verticalAlign: "middle",
    margin: 10,
    minWidth: 210,
  },
  scoreCurve: {
    marginTop: "auto",
    marginBottom: "auto",
  },
});

export const OverallSummary = () => {
  const classes = useStyles();

  return (
    <Box
      className={`${classes.root} ${classes.horizontalStack} ${classes.spread}`}
    >
      <Box className={`${classes.verticalStack} ${classes.scoreCurve}`}>
        <Typography variant="h4" className={classes.overallLabel}>
          Overall Scaled Score
        </Typography>
        <ScoreDistribution
          height={250}
          width={500}
          margin={{ top: 10, left: 10, bottom: 20, right: 10 }}
          leftLabel={OverallResults.minScaledScore.toString()}
          rightLabel={OverallResults.maxScaledScore.toString()}
          score={OverallResults.scaledScore.toString()}
          percentage={OverallResults.percent}
        />
      </Box>
      <Slide direction="left" in={true} timeout={500}>
        <Grid
          container
          direction="row"
          justify="center"
          alignItems="center"
          className={classes.detailsGrid}
        >
          <Grid item>
            <Card variant="outlined" className={classes.detailCard}>
              <CardContent>
                <Typography variant="h4">
                  {OverallResults.scaledScore}
                </Typography>
                <Typography variant="subtitle2" display="inline">
                  Scaled Score
                </Typography>
                <ScaledScoreHelp forSection={false} />
              </CardContent>
            </Card>
          </Grid>
          <Grid item>
            <Card variant="outlined" className={classes.detailCard}>
              <CardContent>
                <Typography variant="h4">{OverallResults.percent}%</Typography>
                <Typography variant="subtitle2" display="inline">
                  Percentile Rank
                </Typography>
                <PercentileRankHelp />
              </CardContent>
            </Card>
          </Grid>
          <Grid item>
            <Card variant="outlined" className={classes.detailCard}>
              <CardContent>
                <Typography variant="h4">
                  {OverallResults.questionsCorrect} of{" "}
                  {OverallResults.totalQuestions}
                </Typography>
                <Typography variant="subtitle2">Questions Correct</Typography>
              </CardContent>
            </Card>
          </Grid>
          <Grid item>
            <Card variant="outlined" className={classes.detailCard}>
              <CardContent>
                <Typography variant="h4">
                  {moment
                    .utc(
                      moment
                        .duration(OverallResults.timeSpentInSeconds, "seconds")
                        .asMilliseconds()
                    )
                    .format("hh:mm:ss")}
                </Typography>
                <Typography variant="subtitle2">Time Spent</Typography>
              </CardContent>
            </Card>
          </Grid>
          <Grid item>
            <Card variant="outlined" className={classes.detailCard}>
              <CardContent>
                <Typography variant="h4">
                  {new Date(OverallResults.dateAttempted).toLocaleDateString()}
                </Typography>
                <Typography variant="subtitle2">Attempt Date</Typography>
              </CardContent>
            </Card>
          </Grid>
        </Grid>
      </Slide>
    </Box>
  );
};
