import { Identifier, Sentence } from "./Text";

export class SkillGroup {
  public label: string;
  public description: string;
  public skills: Array<SkillData>;
  constructor() {
    this.label = Identifier(3);
    this.description = Sentence(2);
    this.skills = [...new Array(3 + Math.ceil(Math.random() * 2))].map(
      () => new SkillData()
    );
  }

  public totalItems = () =>
    this.skills.reduce((total, skill) => total + skill.items, 0);
}

export class SkillData {
  public correct: number;
  public skill: Skill;
  public items: number;
  constructor() {
    this.skill = new Skill();
    this.items = 2 + Math.ceil(Math.random() * 8);
    this.correct =
      Math.floor(this.items / 2) + Math.floor(Math.random() * (this.items / 2));
  }
}

export class Skill {
  public label: string;
  public description: string;
  constructor() {
    this.label = Identifier(4);
    this.description = Sentence(1);
  }
}

export const SkillsGroups: Array<SkillGroup> = [
  ...new Array(3 + Math.ceil(Math.random() * 2)),
].map(() => new SkillGroup());
