import {
  IconButton,
  makeStyles,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableRow,
  Typography,
} from "@material-ui/core";
import VisibilityIcon from "@material-ui/icons/Visibility";
import React from "react";
import { SkillData } from "../Data/Skills";

const useStyles = makeStyles({
  acronym: {
    width: "10%",
  },
  description: {},
  items: {
    textAlign: "center",
    width: "10%",
  },
  correct: {
    textAlign: "center",
    width: "10%",
  },
  centered: {
    textAlign: "center",
  },
  review: {
    width: "5%",
  },
});

export interface SkillsBreakdownTableProperties {
  skills: Array<SkillData>;
  onReview: (start: number, number: number) => void;
}

export const SkillsBreakdownTable = ({
  skills,
  onReview,
}: SkillsBreakdownTableProperties) => {
  const classes = useStyles();

  const getRangeStart = (skillIndex: number) =>
    skills.reduce(
      (total, skill, index) =>
        index < skillIndex ? total + skill.items : total,
      0
    );

  return (
    <Table>
      <TableHead>
        <TableRow>
          <TableCell className={classes.acronym}>
            <Typography>Acronym</Typography>
          </TableCell>
          <TableCell className={classes.description}>
            <Typography>Description</Typography>
          </TableCell>
          <TableCell className={classes.items}>
            <Typography>Items</Typography>
          </TableCell>
          <TableCell className={classes.correct}>
            <Typography>Correct</Typography>
          </TableCell>
          <TableCell className={classes.review}></TableCell>
        </TableRow>
      </TableHead>
      <TableBody>
        {skills.map((skill, index) => (
          <TableRow key={index}>
            <TableCell>
              <Typography>{skill.skill.label}</Typography>
            </TableCell>
            <TableCell>
              <Typography>{skill.skill.description}</Typography>
            </TableCell>
            <TableCell>
              <Typography className={classes.centered}>
                {skill.items}
              </Typography>
            </TableCell>
            <TableCell>
              <Typography className={classes.centered}>
                {skill.correct}
              </Typography>
            </TableCell>
            <TableCell className={classes.centered}>
              <IconButton
                onClick={() => onReview(getRangeStart(index), skill.items)}
              >
                <VisibilityIcon />
              </IconButton>
            </TableCell>
          </TableRow>
        ))}
      </TableBody>
    </Table>
  );
};
